import helper from '../../common/helper.js'
export default{
	data(){
		return{
			id:null,
			session:null
			
		}
	},
	methods:{
		getCollection:function(){
			let that = this;
			
			
			
			let session = uni.getStorageSync('session');
			let openid = uni.getStorageSync('id');
			console.log("sessionId:");
			console.log(session);
			console.log("openid:");
			console.log(openid);
			uni.request({
				url:helper.webroot + "api/my/collection",
				method:"POST",
				header:{
					session:session
				},
				data:{
					openid:openid
				},
				success:function(res){
					console.log("res");
					console.log(that.session);
					
					
				}
			})
			
		}
	},
	onLoad:function(){
		let that = this;
		uni.getStorage({
			key:'id',
			success:function(res){
				that.id = res.data;
			}
		})
		uni.getStorage({
			key:'session',
			success:function(res){
				that.session = res.data;
			}
		})
		
		that.getCollection();
	}
}
